.ArtistCard {
    position: fixed;
    height: auto;
    width: 240px;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    padding: 0;
    color: white;
    display: none;
    background-color: var(--main-color);
}

.ArtistCard .artistInfo {
    position: absolute;
    height: auto;
    padding: 0 1em;
    top: 100%;
    width: calc(100% - 2em);
    -webkit-transition: all .2s;
    -o-transition: all .2s;
    transition: all .2s;
    z-index: -1;
    background-color: var(--main-color);
    -webkit-transform: translateY(-100%);
        -ms-transform: translateY(-100%);
            transform: translateY(-100%);
}

.ArtistCard .artistInfo.active {
    -webkit-transform: translateY(0);
        -ms-transform: translateY(0);
            transform: translateY(0);
}

.ArtistCard a {
    color: white;
    display: block;
    text-align: center;
    width: calc(100% - 1.6em);
    padding: .5em .8em;
    background-color: var(--main-color-dark);
}


.ArtistCard h1 {
    margin: .5em;
    position: relative;
    text-align: center;
    z-index: 5;
}

.ArtistCard .image {
    position: relative;
    z-index: 5;
    width: 240px;
    height: 240px;
}

.ArtistCard.active {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
}

.ArtistCard.active::after {
    content: ' ';
    display: block;
    position: fixed;
    top: -50vh;
    right: -50vw;
    bottom: -50vh;
    left: -50vw;
    background-color: inherit;
    opacity: .2;

}


